.App {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.chartheading{
  font-weight: 700;
  margin-top: 15px;
  margin-bottom: 7px;
}
text.tickLabel {
  transform: translate3d(5px, 20px, 0px) rotate(-90deg);
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-container {
  padding: 30px;
}

.App-link {
  color: #61dafb;
}


.statscontainer{
  margin-bottom: 5px;
  padding: 10px;
}

.datacontainer{
  margin-bottom: 5px;
  margin-top:7px;
  padding: 10px;
  background-color: #efefef;
  border: 1px solid lightgray;
}

.statsHeading{
  font-size: 1.6em;
  font-weight: 700;
  margin-bottom:12px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif
}

.statsInfo{
  font-size: 1.1em;
  line-height: 1.8em;
  margin-bottom:10px;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
